"use client";

import { useState } from 'react';

interface Exercise {
  id: string;
  name: string;
  series: string;
  lastLoad: string;
  progress: string;
  progressType: 'positive' | 'negative' | 'neutral' | 'new';
}

interface ExerciseCardProps {
  exercise: Exercise;
}

export default function ExerciseCard({ exercise }: ExerciseCardProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [currentLoad, setCurrentLoad] = useState(
    parseInt(exercise.lastLoad.replace(/\D/g, '')) || 0
  );

  const getProgressColor = (type: string) => {
    switch (type) {
      case 'positive':
        return 'bg-green-100 text-green-700';
      case 'negative':
        return 'bg-red-100 text-red-700';
      case 'new':
        return 'bg-yellow-100 text-yellow-700';
      default:
        return 'bg-blue-100 text-blue-700';
    }
  };

  const incrementLoad = () => {
    if (currentLoad < 200) {
      setCurrentLoad(currentLoad + 1);
    }
  };

  const decrementLoad = () => {
    if (currentLoad > 0) {
      setCurrentLoad(currentLoad - 1);
    }
  };

  const handleRegisterWorkout = () => {
    const workoutData = {
      exerciseId: exercise.id,
      exerciseName: exercise.name,
      load: currentLoad,
      date: new Date().toISOString(),
      completed: true
    };
    
    // Save to localStorage for persistence
    const existingWorkouts = JSON.parse(localStorage.getItem('workoutHistory') || '[]');
    existingWorkouts.push(workoutData);
    localStorage.setItem('workoutHistory', JSON.stringify(existingWorkouts));
    
    // Show success message
    alert(`Treino registrado com sucesso!\n${exercise.name} - ${currentLoad}kg`);
    
    // Close expanded view
    setIsExpanded(false);
  };

  return (
    <div className="bg-white rounded-xl p-4 shadow-sm border-l-4 border-blue-500 transition-all duration-300 hover:shadow-md hover:-translate-y-1 cursor-pointer">
      <div 
        className="flex items-start justify-between cursor-pointer"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className="flex items-start flex-1">
          <div className="mr-4 mt-1">
            <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center text-blue-600">
              <span className="font-bold text-lg">üí™</span>
            </div>
          </div>
          <div className="flex-1">
            <h3 className="font-semibold text-gray-800">{exercise.name}</h3>
            <p className="text-sm text-gray-600">{exercise.series}</p>
            <div className="mt-2 flex items-center flex-wrap gap-2">
              <span className="text-xs bg-gray-100 px-2 py-1 rounded">
                √öltima carga: {exercise.lastLoad}
              </span>
              <span className={`text-xs px-2 py-1 rounded ${getProgressColor(exercise.progressType)}`}>
                {exercise.progress}
              </span>
            </div>
          </div>
        </div>
        <div className="text-blue-600 p-1">
          <span className={`transform transition-transform ${isExpanded ? 'rotate-90' : ''}`}>
            ‚ñ∂
          </span>
        </div>
      </div>
      
      {isExpanded && (
        <div className="mt-4 border-t pt-4 animate-in slide-in-from-top-2 duration-200">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Carga atual (kg)
              </label>
              <div className="flex items-center">
                <button 
                  onClick={decrementLoad}
                  className="w-8 h-8 rounded-l bg-gray-200 text-gray-600 flex items-center justify-center hover:bg-gray-300 transition-colors"
                  disabled={currentLoad <= 0}
                >
                  -
                </button>
                <input 
                  type="number" 
                  value={currentLoad}
                  onChange={(e) => setCurrentLoad(Math.max(0, Math.min(200, parseInt(e.target.value) || 0)))}
                  className="w-16 h-8 text-center border-t border-b border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <button 
                  onClick={incrementLoad}
                  className="w-8 h-8 rounded-r bg-gray-200 text-gray-600 flex items-center justify-center hover:bg-gray-300 transition-colors"
                  disabled={currentLoad >= 200}
                >
                  +
                </button>
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Progresso
              </label>
              <div className="flex items-center">
                <div className="w-4 h-4 bg-green-500 rounded mr-2"></div>
                <span className="text-sm">75% da meta</span>
              </div>
            </div>
          </div>
          <button 
            onClick={handleRegisterWorkout}
            className="mt-4 w-full bg-blue-600 text-white py-2 rounded-lg font-medium hover:bg-blue-700 transition-colors"
          >
            Registrar Treino
          </button>
        </div>
      )}
    </div>
  );
}
