"use client";

import { useState, useEffect } from 'react';

interface ProfileModalProps {
  isOpen: boolean;
  onClose: () => void;
  onLogout: () => void;
}

interface UserProfile {
  name: string;
  age: number;
  weight: number;
  height: number;
  goal: string;
  trainingTime: string;
  startDate: string;
  level: string;
}

export default function ProfileModal({ isOpen, onClose, onLogout }: ProfileModalProps) {
  const [profile, setProfile] = useState<UserProfile>({
    name: 'Jo√£o Silva',
    age: 28,
    weight: 75,
    height: 175,
    goal: 'Ganho de massa muscular',
    trainingTime: '6 meses',
    startDate: '2024-07-01',
    level: 'Intermedi√°rio'
  });

  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    if (isOpen) {
      // Load profile from localStorage
      const savedProfile = localStorage.getItem('userProfile');
      if (savedProfile) {
        setProfile(JSON.parse(savedProfile));
      }
      
      // Disable background scrolling
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const handleSave = () => {
    localStorage.setItem('userProfile', JSON.stringify(profile));
    setIsEditing(false);
    alert('Perfil atualizado com sucesso!');
  };

  const calculateBMI = () => {
    const heightInMeters = profile.height / 100;
    const bmi = profile.weight / (heightInMeters * heightInMeters);
    return bmi.toFixed(1);
  };

  const getBMICategory = (bmi: number) => {
    if (bmi < 18.5) return { category: 'Abaixo do peso', color: 'text-blue-600' };
    if (bmi < 25) return { category: 'Peso normal', color: 'text-green-600' };
    if (bmi < 30) return { category: 'Sobrepeso', color: 'text-yellow-600' };
    return { category: 'Obesidade', color: 'text-red-600' };
  };

  const bmi = parseFloat(calculateBMI());
  const bmiInfo = getBMICategory(bmi);

  const calculateTrainingDays = () => {
    const startDate = new Date(profile.startDate);
    const today = new Date();
    const diffTime = Math.abs(today.getTime() - startDate.getTime());
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
  };

  return (
    <div 
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-auto animate-in zoom-in-95 duration-200">
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 flex items-center">
              <span className="text-2xl mr-2">üë§</span>
              Meu Perfil
            </h2>
            <div className="flex items-center gap-2">
              {!isEditing ? (
                <button 
                  onClick={() => setIsEditing(true)}
                  className="px-3 py-1 bg-blue-100 text-blue-600 rounded-lg text-sm hover:bg-blue-200 transition-colors"
                >
                  Editar
                </button>
              ) : (
                <div className="flex gap-2">
                  <button 
                    onClick={() => setIsEditing(false)}
                    className="px-3 py-1 bg-gray-100 text-gray-600 rounded-lg text-sm hover:bg-gray-200 transition-colors"
                  >
                    Cancelar
                  </button>
                  <button 
                    onClick={handleSave}
                    className="px-3 py-1 bg-green-100 text-green-600 rounded-lg text-sm hover:bg-green-200 transition-colors"
                  >
                    Salvar
                  </button>
                </div>
              )}
              <button 
                onClick={onLogout}
                className="px-3 py-1 bg-red-100 text-red-600 rounded-lg text-sm hover:bg-red-200 transition-colors"
              >
                Sair
              </button>
              <button 
                onClick={onClose}
                className="text-gray-600 hover:text-gray-800 text-2xl font-bold w-8 h-8 flex items-center justify-center rounded-full hover:bg-gray-100 transition-colors"
              >
                √ó
              </button>
            </div>
          </div>

          {/* Profile Info */}
          <div className="space-y-6">
            {/* Basic Info */}
            <div className="bg-gray-50 rounded-lg p-4">
              <h3 className="font-medium text-gray-800 mb-4 flex items-center">
                <span className="text-xl mr-2">üìã</span>
                Informa√ß√µes B√°sicas
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Nome</label>
                  {isEditing ? (
                    <input
                      type="text"
                      value={profile.name}
                      onChange={(e) => setProfile({...profile, name: e.target.value})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    />
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.name}</p>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Idade</label>
                  {isEditing ? (
                    <input
                      type="number"
                      value={profile.age}
                      onChange={(e) => setProfile({...profile, age: parseInt(e.target.value) || 0})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    />
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.age} anos</p>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Peso (kg)</label>
                  {isEditing ? (
                    <input
                      type="number"
                      value={profile.weight}
                      onChange={(e) => setProfile({...profile, weight: parseFloat(e.target.value) || 0})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    />
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.weight} kg</p>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Altura (cm)</label>
                  {isEditing ? (
                    <input
                      type="number"
                      value={profile.height}
                      onChange={(e) => setProfile({...profile, height: parseInt(e.target.value) || 0})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    />
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.height} cm</p>
                  )}
                </div>
              </div>
            </div>

            {/* BMI Info */}
            <div className="bg-blue-50 rounded-lg p-4">
              <h3 className="font-medium text-blue-800 mb-3 flex items-center">
                <span className="text-xl mr-2">‚öñÔ∏è</span>
                √çndice de Massa Corporal (IMC)
              </h3>
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-2xl font-bold text-blue-600">{calculateBMI()}</p>
                  <p className={`text-sm font-medium ${bmiInfo.color}`}>{bmiInfo.category}</p>
                </div>
                <div className="text-right text-sm text-blue-700">
                  <p>Peso: {profile.weight} kg</p>
                  <p>Altura: {(profile.height / 100).toFixed(2)} m</p>
                </div>
              </div>
            </div>

            {/* Training Info */}
            <div className="bg-green-50 rounded-lg p-4">
              <h3 className="font-medium text-green-800 mb-4 flex items-center">
                <span className="text-xl mr-2">üèãÔ∏è</span>
                Informa√ß√µes de Treino
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Objetivo</label>
                  {isEditing ? (
                    <select
                      value={profile.goal}
                      onChange={(e) => setProfile({...profile, goal: e.target.value})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    >
                      <option value="Ganho de massa muscular">Ganho de massa muscular</option>
                      <option value="Perda de peso">Perda de peso</option>
                      <option value="Defini√ß√£o muscular">Defini√ß√£o muscular</option>
                      <option value="Condicionamento f√≠sico">Condicionamento f√≠sico</option>
                      <option value="Manuten√ß√£o">Manuten√ß√£o</option>
                    </select>
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.goal}</p>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">N√≠vel</label>
                  {isEditing ? (
                    <select
                      value={profile.level}
                      onChange={(e) => setProfile({...profile, level: e.target.value})}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    >
                      <option value="Iniciante">Iniciante</option>
                      <option value="Intermedi√°rio">Intermedi√°rio</option>
                      <option value="Avan√ßado">Avan√ßado</option>
                    </select>
                  ) : (
                    <p className="text-gray-800 font-medium">{profile.level}</p>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Tempo de Treino</label>
                  <p className="text-gray-800 font-medium">{profile.trainingTime}</p>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Dias Treinando</label>
                  <p className="text-gray-800 font-medium">{calculateTrainingDays()} dias</p>
                </div>
              </div>
            </div>

            {/* Stats */}
            <div className="bg-purple-50 rounded-lg p-4">
              <h3 className="font-medium text-purple-800 mb-3 flex items-center">
                <span className="text-xl mr-2">üìà</span>
                Estat√≠sticas R√°pidas
              </h3>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <div className="text-center">
                  <p className="text-2xl font-bold text-purple-600">
                    {JSON.parse(localStorage.getItem('attendanceHistory') || '[]').length}
                  </p>
                  <p className="text-xs text-purple-700">Treinos Registrados</p>
                </div>
                <div className="text-center">
                  <p className="text-2xl font-bold text-purple-600">
                    {JSON.parse(localStorage.getItem('attendanceHistory') || '[]').filter((record: any) => {
                      const recordDate = new Date(record.date);
                      const weekAgo = new Date();
                      weekAgo.setDate(weekAgo.getDate() - 7);
                      return recordDate > weekAgo;
                    }).length}
                  </p>
                  <p className="text-xs text-purple-700">Esta Semana</p>
                </div>
                <div className="text-center">
                  <p className="text-2xl font-bold text-purple-600">{calculateBMI()}</p>
                  <p className="text-xs text-purple-700">IMC Atual</p>
                </div>
                <div className="text-center">
                  <p className="text-2xl font-bold text-purple-600">{calculateTrainingDays()}</p>
                  <p className="text-xs text-purple-700">Dias Ativos</p>
                </div>
              </div>
            </div>
          </div>

          {/* Close Button */}
          <div className="mt-6">
            <button 
              onClick={onClose}
              className="w-full px-4 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Fechar
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
