"use client";

import { useState } from 'react';

interface BottomNavProps {
  onCalendarClick: () => void;
  onStatsClick: () => void;
  onProfileClick: () => void;
}

export default function BottomNav({ onCalendarClick, onStatsClick, onProfileClick }: BottomNavProps) {
  const [activeTab, setActiveTab] = useState('inicio');

  const navItems = [
    { id: 'inicio', label: 'In√≠cio', icon: '' },
    { id: 'calendario', label: 'Calend√°rio', icon: '' },
    { id: 'estatisticas', label: 'Estat√≠sticas', icon: '' },
    { id: 'perfil', label: 'Perfil', icon: 'üë§' }
  ];

  const handleNavClick = (itemId: string) => {
    setActiveTab(itemId);
    
    if (itemId === 'calendario') {
      onCalendarClick();
    } else if (itemId === 'estatisticas') {
      onStatsClick();
    } else if (itemId === 'perfil') {
      onProfileClick();
    }
  };

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 py-2 px-4 z-40">
      <div className="flex justify-around max-w-md mx-auto">
        {navItems.map((item) => (
          <button
            key={item.id}
            onClick={() => handleNavClick(item.id)}
            className={`flex flex-col items-center py-2 px-3 rounded-lg transition-colors ${
              activeTab === item.id
                ? 'text-blue-600 bg-blue-50'
                : 'text-gray-500 hover:text-gray-700 hover:bg-gray-50'
            }`}
          >
            <span className="text-xl mb-1">{item.icon}</span>
            <span className="text-xs font-medium">{item.label}</span>
          </button>
        ))}
      </div>
    </div>
  );
}
