"use client";

import { useState, useEffect } from 'react';

interface AttendanceTrackerProps {
  isOpen: boolean;
  onClose: () => void;
  onAttendanceRegistered: () => void;
}

interface AttendanceRecord {
  id: string;
  date: string;
  workoutType: string;
  duration: number;
  exercises: string[];
  notes?: string;
}

export default function AttendanceTracker({ isOpen, onClose, onAttendanceRegistered }: AttendanceTrackerProps) {
  const [workoutType, setWorkoutType] = useState('');
  const [duration, setDuration] = useState(60);
  const [notes, setNotes] = useState('');
  const [isRegistering, setIsRegistering] = useState(false);

  const workoutTypes = [
    'Costas e B√≠ceps',
    'Peito e Tr√≠ceps', 
    'Pernas',
    'Ombros e Trap√©zio',
    'Bra√ßos',
    'Cardio e Core',
    'Treino Livre'
  ];

  useEffect(() => {
    if (isOpen) {
      // Disable background scrolling
      document.body.style.overflow = 'hidden';
      // Set default workout type based on current day
      const today = new Date().getDay();
      const dayWorkouts = {
        1: 'Costas e B√≠ceps', // Segunda
        2: 'Peito e Tr√≠ceps', // Ter√ßa
        3: 'Pernas', // Quarta
        4: 'Ombros e Trap√©zio', // Quinta
        5: 'Bra√ßos', // Sexta
        6: 'Cardio e Core' // S√°bado
      };
      setWorkoutType(dayWorkouts[today as keyof typeof dayWorkouts] || 'Treino Livre');
    }

    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const handleRegisterAttendance = async () => {
    setIsRegistering(true);
    
    const attendanceRecord: AttendanceRecord = {
      id: Date.now().toString(),
      date: new Date().toISOString(),
      workoutType,
      duration,
      exercises: [], // Will be populated from workout data
      notes
    };

    // Save to localStorage
    const existingRecords = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
    existingRecords.push(attendanceRecord);
    localStorage.setItem('attendanceHistory', JSON.stringify(existingRecords));

    // Also update workout history for compatibility
    const workoutHistory = JSON.parse(localStorage.getItem('workoutHistory') || '[]');
    workoutHistory.push({
      exerciseId: 'attendance',
      exerciseName: workoutType,
      load: 0,
      date: attendanceRecord.date,
      completed: true,
      duration: duration
    });
    localStorage.setItem('workoutHistory', JSON.stringify(workoutHistory));

    setTimeout(() => {
      setIsRegistering(false);
      onAttendanceRegistered();
      onClose();
      
      // Show success message
      alert(`‚úÖ Frequ√™ncia registrada!\n\nTreino: ${workoutType}\nDura√ß√£o: ${duration} minutos\nData: ${new Date().toLocaleDateString('pt-BR')}`);
    }, 1000);
  };

  return (
    <div 
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full animate-in zoom-in-95 duration-200">
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 flex items-center">
              <span className="text-2xl mr-2">üìù</span>
              Registrar Frequ√™ncia
            </h2>
            <button 
              onClick={onClose}
              className="text-gray-600 hover:text-gray-800 text-2xl font-bold w-8 h-8 flex items-center justify-center rounded-full hover:bg-gray-100 transition-colors"
            >
              √ó
            </button>
          </div>

          {/* Form */}
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tipo de Treino
              </label>
              <select
                value={workoutType}
                onChange={(e) => setWorkoutType(e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                {workoutTypes.map((type) => (
                  <option key={type} value={type}>{type}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Dura√ß√£o (minutos)
              </label>
              <div className="flex items-center">
                <button
                  onClick={() => setDuration(Math.max(15, duration - 15))}
                  className="w-10 h-10 rounded-l bg-gray-200 text-gray-600 flex items-center justify-center hover:bg-gray-300 transition-colors"
                >
                  -
                </button>
                <input
                  type="number"
                  value={duration}
                  onChange={(e) => setDuration(Math.max(15, Math.min(180, parseInt(e.target.value) || 60)))}
                  className="flex-1 h-10 text-center border-t border-b border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  min="15"
                  max="180"
                />
                <button
                  onClick={() => setDuration(Math.min(180, duration + 15))}
                  className="w-10 h-10 rounded-r bg-gray-200 text-gray-600 flex items-center justify-center hover:bg-gray-300 transition-colors"
                >
                  +
                </button>
              </div>
              <p className="text-xs text-gray-500 mt-1">Entre 15 e 180 minutos</p>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Observa√ß√µes (opcional)
              </label>
              <textarea
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                placeholder="Como foi o treino? Alguma observa√ß√£o..."
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
                rows={3}
              />
            </div>

            {/* Current Date Display */}
            <div className="bg-blue-50 rounded-lg p-3">
              <div className="flex items-center justify-between">
                <span className="text-sm text-blue-700">Data do registro:</span>
                <span className="font-medium text-blue-800">
                  {new Date().toLocaleDateString('pt-BR', { 
                    weekday: 'long', 
                    year: 'numeric', 
                    month: 'long', 
                    day: 'numeric' 
                  })}
                </span>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-3 mt-6">
            <button 
              onClick={onClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
              disabled={isRegistering}
            >
              Cancelar
            </button>
            <button 
              onClick={handleRegisterAttendance}
              disabled={!workoutType || isRegistering}
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
            >
              {isRegistering ? (
                <>
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                  Registrando...
                </>
              ) : (
                'Registrar Frequ√™ncia'
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
