"use client";

import { useState, useEffect } from 'react';

interface StatsModalProps {
  isOpen: boolean;
  onClose: () => void;
}

interface WorkoutStats {
  totalWorkouts: number;
  thisWeek: number;
  thisMonth: number;
  weeklyGoal: number;
  monthlyGoal: number;
  currentStreak: number;
  longestStreak: number;
  favoriteExercise: string;
  totalWeight: number;
  averageWorkoutTime: number;
}

export default function StatsModal({ isOpen, onClose }: StatsModalProps) {
  const [stats, setStats] = useState<WorkoutStats>({
    totalWorkouts: 0,
    thisWeek: 0,
    thisMonth: 0,
    weeklyGoal: 4,
    monthlyGoal: 16,
    currentStreak: 0,
    longestStreak: 0,
    favoriteExercise: "Supino Reto",
    totalWeight: 0,
    averageWorkoutTime: 55
  });

  useEffect(() => {
    if (isOpen) {
      // Get attendance history from localStorage
      const attendanceHistory = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
      const workoutHistory = JSON.parse(localStorage.getItem('workoutHistory') || '[]');
      
      const now = new Date();
      const weekAgo = new Date();
      weekAgo.setDate(weekAgo.getDate() - 7);
      
      const monthAgo = new Date();
      monthAgo.setMonth(monthAgo.getMonth() - 1);

      // Filter attendance records for this week and month
      const thisWeekAttendance = attendanceHistory.filter((record: any) => {
        const recordDate = new Date(record.date);
        return recordDate > weekAgo;
      });

      const thisMonthAttendance = attendanceHistory.filter((record: any) => {
        const recordDate = new Date(record.date);
        return recordDate > monthAgo;
      });

      // Calculate total weight from exercise records
      const totalWeight = workoutHistory.reduce((sum: number, workout: any) => {
        return sum + (workout.load || 0);
      }, 0);

      // Calculate average workout time from attendance records
      const avgTime = attendanceHistory.length > 0 
        ? attendanceHistory.reduce((sum: number, record: any) => sum + (record.duration || 55), 0) / attendanceHistory.length
        : 55;

      // Calculate current streak (consecutive days with workouts)
      const sortedAttendance = attendanceHistory
        .map((record: any) => new Date(record.date))
        .sort((a: Date, b: Date) => b.getTime() - a.getTime());

      let currentStreak = 0;
      let longestStreak = 0;
      let tempStreak = 0;

      if (sortedAttendance.length > 0) {
        const today = new Date();
        today.setHours(0, 0, 0, 0);
        
        for (let i = 0; i < sortedAttendance.length; i++) {
          const recordDate = new Date(sortedAttendance[i]);
          recordDate.setHours(0, 0, 0, 0);
          
          const daysDiff = Math.floor((today.getTime() - recordDate.getTime()) / (1000 * 60 * 60 * 24));
          
          if (i === 0 && daysDiff <= 1) {
            currentStreak = 1;
            tempStreak = 1;
          } else if (i > 0) {
            const prevDate = new Date(sortedAttendance[i - 1]);
            prevDate.setHours(0, 0, 0, 0);
            const daysBetween = Math.floor((prevDate.getTime() - recordDate.getTime()) / (1000 * 60 * 60 * 24));
            
            if (daysBetween === 1) {
              tempStreak++;
              if (i === currentStreak) currentStreak++;
            } else {
              longestStreak = Math.max(longestStreak, tempStreak);
              tempStreak = 1;
            }
          }
        }
        longestStreak = Math.max(longestStreak, tempStreak);
      }

      // Find most frequent workout type
      const workoutTypeCounts = attendanceHistory.reduce((counts: any, record: any) => {
        counts[record.workoutType] = (counts[record.workoutType] || 0) + 1;
        return counts;
      }, {});

      const favoriteExercise = Object.keys(workoutTypeCounts).length > 0
        ? Object.keys(workoutTypeCounts).reduce((a, b) => workoutTypeCounts[a] > workoutTypeCounts[b] ? a : b)
        : "Supino Reto";

      setStats({
        totalWorkouts: attendanceHistory.length,
        thisWeek: thisWeekAttendance.length,
        thisMonth: thisMonthAttendance.length,
        weeklyGoal: 4,
        monthlyGoal: 16,
        currentStreak: currentStreak,
        longestStreak: longestStreak,
        favoriteExercise: favoriteExercise,
        totalWeight: totalWeight,
        averageWorkoutTime: Math.round(avgTime)
      });

      // Disable background scrolling
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const weeklyProgress = (stats.thisWeek / stats.weeklyGoal) * 100;
  const monthlyProgress = (stats.thisMonth / stats.monthlyGoal) * 100;

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div 
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-auto animate-in zoom-in-95 duration-200">
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 flex items-center">
              <span className="text-2xl mr-2">üìä</span>
              Suas Estat√≠sticas
            </h2>
            <button 
              onClick={onClose}
              className="text-gray-600 hover:text-gray-800 text-2xl font-bold w-8 h-8 flex items-center justify-center rounded-full hover:bg-gray-100 transition-colors"
            >
              √ó
            </button>
          </div>

          {/* Progress Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
            {/* Weekly Progress */}
            <div className="bg-blue-50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <h3 className="font-medium text-blue-800">Meta Semanal</h3>
                <span className="text-2xl">üéØ</span>
              </div>
              <div className="flex items-center justify-between mb-2">
                <span className="text-2xl font-bold text-blue-600">{stats.thisWeek}/{stats.weeklyGoal}</span>
                <span className="text-sm text-blue-600">{Math.round(weeklyProgress)}%</span>
              </div>
              <div className="w-full bg-blue-200 rounded-full h-2">
                <div 
                  className="bg-blue-600 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${Math.min(weeklyProgress, 100)}%` }}
                ></div>
              </div>
              <p className="text-xs text-blue-700 mt-1">Treinos esta semana</p>
            </div>

            {/* Monthly Progress */}
            <div className="bg-green-50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <h3 className="font-medium text-green-800">Meta Mensal</h3>
                <span className="text-2xl">üìÖ</span>
              </div>
              <div className="flex items-center justify-between mb-2">
                <span className="text-2xl font-bold text-green-600">{stats.thisMonth}/{stats.monthlyGoal}</span>
                <span className="text-sm text-green-600">{Math.round(monthlyProgress)}%</span>
              </div>
              <div className="w-full bg-green-200 rounded-full h-2">
                <div 
                  className="bg-green-600 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${Math.min(monthlyProgress, 100)}%` }}
                ></div>
              </div>
              <p className="text-xs text-green-700 mt-1">Treinos este m√™s</p>
            </div>
          </div>

          {/* Stats Grid */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
            <div className="bg-gray-50 rounded-lg p-4 text-center">
              <div className="text-2xl mb-1">üèãÔ∏è</div>
              <div className="text-2xl font-bold text-gray-800">{stats.totalWorkouts}</div>
              <div className="text-xs text-gray-600">Total de Treinos</div>
            </div>

            <div className="bg-orange-50 rounded-lg p-4 text-center">
              <div className="text-2xl mb-1">üî•</div>
              <div className="text-2xl font-bold text-orange-600">{stats.currentStreak}</div>
              <div className="text-xs text-orange-600">Sequ√™ncia Atual</div>
            </div>

            <div className="bg-purple-50 rounded-lg p-4 text-center">
              <div className="text-2xl mb-1">‚ö°</div>
              <div className="text-2xl font-bold text-purple-600">{stats.longestStreak}</div>
              <div className="text-xs text-purple-600">Maior Sequ√™ncia</div>
            </div>

            <div className="bg-red-50 rounded-lg p-4 text-center">
              <div className="text-2xl mb-1">‚è±Ô∏è</div>
              <div className="text-2xl font-bold text-red-600">{stats.averageWorkoutTime}min</div>
              <div className="text-xs text-red-600">Tempo M√©dio</div>
            </div>
          </div>

          {/* Detailed Stats */}
          <div className="space-y-4">
            <div className="bg-gray-50 rounded-lg p-4">
              <h3 className="font-medium text-gray-800 mb-3 flex items-center">
                <span className="text-xl mr-2">üí™</span>
                Desempenho
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="flex justify-between">
                  <span className="text-gray-600">Exerc√≠cio Favorito:</span>
                  <span className="font-medium">{stats.favoriteExercise}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600">Peso Total Levantado:</span>
                  <span className="font-medium">{stats.totalWeight}kg</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600">Frequ√™ncia Semanal:</span>
                  <span className="font-medium">{stats.thisWeek}x por semana</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600">Consist√™ncia:</span>
                  <span className="font-medium text-green-600">
                    {weeklyProgress >= 75 ? 'Excelente' : weeklyProgress >= 50 ? 'Boa' : 'Precisa melhorar'}
                  </span>
                </div>
              </div>
            </div>

            <div className="bg-blue-50 rounded-lg p-4">
              <h3 className="font-medium text-blue-800 mb-3 flex items-center">
                <span className="text-xl mr-2">üéØ</span>
                Pr√≥ximas Metas
              </h3>
              <div className="space-y-2 text-sm">
                <div className="flex items-center">
                  <div className="w-2 h-2 bg-blue-500 rounded-full mr-2"></div>
                  <span>Completar {stats.weeklyGoal - stats.thisWeek} treinos esta semana</span>
                </div>
                <div className="flex items-center">
                  <div className="w-2 h-2 bg-green-500 rounded-full mr-2"></div>
                  <span>Manter sequ√™ncia de {stats.currentStreak} dias</span>
                </div>
                <div className="flex items-center">
                  <div className="w-2 h-2 bg-purple-500 rounded-full mr-2"></div>
                  <span>Aumentar carga em 5% nos exerc√≠cios principais</span>
                </div>
              </div>
            </div>
          </div>

          {/* Close Button */}
          <div className="mt-6">
            <button 
              onClick={onClose}
              className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Fechar
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
